import { HttpClient } from '@angular/common/http'
import { Component, Input, OnDestroy, OnInit } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import { faFile } from '@fortawesome/pro-regular-svg-icons'
import { faHistory, faReply, faShare, faTrash } from '@fortawesome/pro-solid-svg-icons'
import { TranslateService } from '@ngx-translate/core'
import { merge, Observable, Subject, Subscription } from 'rxjs'
import { finalize, flatMap, switchMap, tap } from 'rxjs/operators'
import { environment } from 'src/environments/environment'
import { File, Mail } from '../../api-interfaces'
import { IntercomService } from '../../services/intercom.service'
import { MailNotificationsService } from '../../services/mail-notifications.service'
import { SessionService } from '../../services/session.service'
import { ToastrService } from '../../services/toastr.service'

@Component({
    selector: 'mail',
    templateUrl: 'mail.component.html',
})
export class MailComponent implements OnInit, OnDestroy {
    @Input()
    public markAsRead = false
    public mail: Mail
    public attachments: any[] = []
    public isDeleting = false
    public isLoading = false
    public displayName = environment.displayName
    public faReply = faReply
    public faShare = faShare
    public faHistory = faHistory
    public faTrash = faTrash
    public faFile = faFile
    private subscriptions = new Subscription()
    private fetchEvent = new Subject<void>()

    constructor(
        private http: HttpClient,
        private Toastr: ToastrService,
        private mailNotifications: MailNotificationsService,
        private route: ActivatedRoute,
        private router: Router,
        private session: SessionService,
        public intercom: IntercomService,
        public translate: TranslateService
    ) {}

    public ngOnInit(): void {
        this.subscriptions.add(this.fetchEvent.pipe(switchMap(() => this.fetch())).subscribe())
        this.subscriptions.add(
            this.route.paramMap.subscribe(params => {
                const mailId = params.get('mailId')
                if (mailId) {
                    this.mail = {
                        id: mailId,
                    } as Mail
                    this.fetchEvent.next()
                }
            })
        )
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public fetch(): Observable<void> {
        this.isLoading = true
        return this.http.get<Mail>(`/mails/${this.mail.id}`).pipe(
            flatMap(mail => {
                Object.assign(this.mail, mail)
                let obs: Observable<any> = this.http.get<any[]>(`/mails/${this.mail.id}/attachments`).pipe(
                    tap(attachments => {
                        this.attachments = attachments
                    })
                )
                if (!this.mail.read && this.markAsRead) {
                    obs = merge(
                        obs,
                        this.http.post(`/mails/${this.mail.id}/mark-as-read`, {}).pipe(
                            tap(() => {
                                this.mailNotifications.notify()
                            })
                        )
                    )
                }
                return obs
            }),
            finalize(() => {
                this.isLoading = false
            })
        )
    }

    public async back(): Promise<void> {
        await this.router.navigate(['../'], { relativeTo: this.route })
    }

    public delete(): void {
        this.isDeleting = true
        this.http
            .delete<void>(`/mails/${this.mail.id}`)
            .pipe(
                finalize(() => {
                    this.isDeleting = false
                })
            )
            .subscribe(async () => {
                this.Toastr.success(this.translate.instant('mail.mail-deleted'))
                await this.back()
            })
    }

    public openAttachment(attachment: File): void {
        this.subscriptions.add(
            this.session
                .createSignedUrl(`/mails/${this.mail.id}/attachments/${attachment.id}`)
                .subscribe(signedUrl => window.open(signedUrl, '_blank', 'noopener'))
        )
    }

    public reply(): void {
        this.intercom.startConversation(this.buildPrePopulatedMessage())
    }

    private buildPrePopulatedMessage(): string {
        return `Subject: ${this.mail.subject}, Message: ${this.mail.content!.replace(/<[^>]+>/g, '')} \n \n`
    }
}
