import { HttpClient } from '@angular/common/http'
import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core'
import { ActivatedRoute, Router } from '@angular/router'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import * as striptags from 'striptags'
import { IUser, Mail } from '../api-interfaces'
import { ListV2Component } from '../list-v2.component'
import { SessionService } from '../services/session.service'

@Component({
    selector: 'mails',
    templateUrl: 'mails.component.html',
})
export class MailsComponent extends ListV2Component<Mail> implements OnInit {
    public striptags = striptags
    @Input()
    public user: IUser | null

    constructor(
        http: HttpClient,
        ngbModal: NgbModal,
        changeDetector: ChangeDetectorRef,
        route: ActivatedRoute,
        router: Router,
        session: SessionService,
        public translate: TranslateService
    ) {
        super(http, ngbModal, changeDetector, route, router, session)
    }

    public ngOnInit(): void {
        super.ngOnInit()
        this.apiUrl = `/users/${this.user!.id}/mails`
    }
}
